@use "./global" as *;

/* FV
***************************************************************/
.lp-connect__fv {
  background: linear-gradient(144deg, #F0F9FB 0%, #E5F5F9 68%, #D7EEF3 100%);
  padding-block: 52px 98px;
  position: relative;
  z-index: 0;

  &::before {
    content: '';
    position: absolute;
    background: url(../img/bg_fv-blur.webp) no-repeat center center / cover;
    width: 458px;
    aspect-ratio: 1;
    bottom: -219px;
    right: calc(50% + 300px);
    z-index: 0;
  }

  @include mq(sp) {
    padding-block: 29px 400px;

    &::before {
      display: none;
    }
  }
}

.lp-connect__fv-inner {
  max-width: calc(1196px + 80px);
  margin-inline: auto;
  padding-inline: 40px;
  position: relative;
  z-index: 1;

  @include mq(sp) {
    padding-inline: 20px;
    max-width: 500px;
  }
}

.lp-connect__fv-title {
  font-size: 45px;
  font-weight: 700;
  line-height: 1.4;
  color: $cl-text;

  @include mq(tb) {
    font-size: min(calc(40 / 1180 * 100vw), 40px);
  }

  @include mq(sp) {
    font-size: min(calc(26 / 375 * 100vw), 26px);
    white-space: nowrap;
    font-feature-settings: "palt" 1;
    translate: -5px 0;
  }

  .lp-connect__fv-title--large {
    font-size: 52px;
    font-weight: 700;
    line-height: 1;
    color: #0098BB;
    display: inline;

    @include mq(tb) {
      font-size: min(calc(46 / 1180 * 100vw), 46px);
    }

    @include mq(sp) {
      font-size: min(calc(30 / 375 * 100vw), 30px);
    }
  }
}

.lp-connect__fv-text {
  margin-top: 57px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  color: #413C3A;

  @include mq(tb) {
    font-size: min(calc(18 / 1180 * 100vw), 18px);
  }

  @include mq(sp) {
    font-size: min(calc(16 / 375 * 100vw), 16px);
    margin-top: 37px;
  }
}

.lp-connect__fv-cta {
  margin-top: 46px;

  @include mq(sp) {
    display: none;
  }
}

.lp-connect__fv-cta--sp {
  display: none;

  @include mq(sp) {
    display: flex;
    gap: 15px;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
  }
}


.lp-connect__fv-img {
  position: absolute;
  max-width: min(calc(806 / 1440 * 100vw), 806px);
  bottom: -16px;
  left: calc(50% - 83px);
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 1612 / 1168;
  }

  @include mq(sp) {
    width: 369px;
    max-width: 100%;
    bottom: 34px;
    left: 50%;
    transform: translateX(-50%);

    img {
      aspect-ratio: 738 / 712;
    }
  }
}