@use "./global" as *;

/* Fixed CTA
***************************************************************/
.lp-connect__fixed-cta {
  display: none;
  @include mq(sp) {
    display: block;
    background: #fff;
    padding-block: 16px;
    transition: all  0.5s ease;
    position: fixed;
    bottom: -1px;
    left: 0;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    z-index: 10;

    &.isShow {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.lp-connect__fixed-cta-inner {
  max-width: 500px;
  padding-inline: 20px;
  margin-inline:auto;
}

.lp-connect__fixed-cta-links {
  display: none;

  @include mq(sp) {
    display: flex;
    gap: 15px;
    justify-content: center;
    align-items: center;
  }
}