@use "./global" as *;

/* おすすめ利用シーンセクション
***************************************************************/
.lp-connect__recommend {
  padding-block: 80px;
  background: #fff;

  @include mq(sp) {
    padding-block: 48px 59px;
  }
}

.lp-connect__recommend-contents {
  display: flex;
  flex-wrap: wrap;
  gap: 32px calc(33 / 968 * 100%);
  justify-content: center;
  align-items: stretch;
  margin-top: 48px;

  @include mq(sp) {
    padding-inline: 17px;
    margin-top: 32px;
    gap: 33px;
  }
}

.lp-connect__recommend-card {
  width: calc(300 / 968 * 100%);
  box-shadow: 2px 2px 15px -1px rgba(0, 0, 0, 0.10);
  border-radius: 20px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: inline-flex;
  border-radius: 20px;
  overflow: hidden;

  @include mq(sp) {
    width: 100%;
  }
}

.lp-connect__recommend-card-img {
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 301 / 202;
    object-fit: cover;
  }
}

.lp-connect__recommend-card-body {
  padding: 24px 20px 27px 20px;
}

.lp-connect__recommend-card-title {
  font-size: 20px;
  font-weight: 600;
  line-height: calc(25 / 20);
  color: #2A2929;
  text-align: center;

  @include mq(tb) {
    font-size: min(calc(20 / 1180 * 100vw), 20px);
  }

  @include mq(sp) {
    font-size: min(calc(20 / 375 * 100vw), 20px);
  }
}

.lp-connect__recommend-card-text {
  color: #59514E;
  font-size: 16px;
  font-weight: 400;
  line-height: calc(26 / 16);
  margin-top: 14px;
  font-feature-settings: "palt" 1;

  @include mq(tb) {
    font-size: min(calc(16 / 1180 * 100vw), 16px);
  }

  @include mq(sp) {
    font-size: min(calc(16 / 375 * 100vw), 16px);
  }
}

.lp-connect__recommend-cta {
  display: none;

  @include mq(sp) {
    display: block;
    text-align: center;
    margin-top: 32px;
  }
}