@use "./global" as *;

/* Problemセクション
***************************************************************/
.lp-connect__problem {
  background: $cl-blue;
  position: relative;
  z-index: 1;
  padding-block: 80px;

  @include mq(sp) {
    padding-block: 48px 60px;
  }
}

.lp-connect__problem-title {
  font-size: 30px;
  font-weight: 900;
  color: $cl-text;
  text-align: center;
  padding-block: 18px 51px;
  position: relative;

  @include mq(sp) {
    padding-block: 7px 27px;
    font-size: min(calc(22 / 375 * 100vw), 22px);
    font-feature-settings: "palt" 1;
  }

  &::before {
    content: '';
    position: absolute;
    background: url(../img/deco_problem-bubble--pc.webp) no-repeat center center / contain;
    width: 690px;
    height: 109px;
    top: 0;
    left: 50%;
    translate: -50% 0;
    z-index: -1;

    @include mq(sp) {
      background: url(../img/deco_problem-bubble--sp.webp) no-repeat center center / contain;
      width: min(calc(335 / 375 * 100vw), 335px);
      height: 75px;
    }
  }
}

.lp-connect__problem-title--large {
  font-size: 38px;
  color: $cl-pink;

  @include mq(sp) {
    font-size: min(calc(26 / 375 * 100vw), 26px);
    line-height: calc(40 / 26);
  }
}

.lp-connect__problem-contents {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: calc(32 / 970 * 100%);
  margin-top: 106px;

  @include mq(sp) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 83px;
    margin-top: 83px;
    padding-inline: 16px;
  }
}

.lp-connect__problem-card {
  padding: 65px 5px 31px 5px;
  background: #fff;
  border-radius: 20px;
  position: relative;

  @include mq(sp) {
    padding: 65px 12px 23px 12px;
  }
}

.lp-connect__problem-card-comment {
  background: $cl-text;
  font-size: 20px;
  font-weight: 700;
  line-height: calc(32 / 20);
  color: #fff;
  text-align: center;
  padding: 16px 8px 19px 8px;
  border-radius: 20px;
  position: absolute;
  top: -59px;
  left: 50%;
  translate: -50% 0;
  width: calc(267 / 302 * 100%);

  @include mq(tb) {
    font-size: min(calc(20 / 1180 * 100vw), 20px);
  }

  @include mq(sp) {
    font-size: min(calc(20 / 375 * 100vw), 20px);
  }

  &::before {
    content: '';
    position: absolute;
    background: url(../img/deco_problem-triangle.webp) no-repeat center center / contain;
    width: 32px;
    aspect-ratio: 32 / 36;
    bottom: -18px;
    left: 50%;
    translate: -50% 0;
  }
}

.lp-connect__problem-card-img {
  position: relative;
  width: 248px;
  max-width: 100%;
  margin-inline:auto;

  @include mq(sp) {
    width: 248px;
  }

  img {
    width: 100%;
    height: auto;
  }

  &::before {
    content: '';
    position: absolute;
    background: url(../img/deco_problem-arrow.webp) no-repeat center center / contain;
    width: 49px;
    aspect-ratio: 49 / 37;
    top: calc(100% + 24px);
    left: 50%;
    translate: -50% 0;

    @include mq(sp) {
      top: calc(100% + 19px);
    }
  }
}

.lp-connect__problem-card-text {
  margin-top: 71px;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.75;
  letter-spacing: -0.03em;
  color: $cl-text;
  text-align: center;

  @include mq(tb) {
    font-size: min(calc(20 / 1180 * 100vw), 20px);
  }

  @include mq(sp) {
    margin-top: 64px;
    font-size: min(calc(20 / 375 * 100vw), 20px);
  }
}

.lp-connect__problem-card-text--large {
  font-size: 26px;
  font-weight: 900;
  line-height: calc(45.5 / 26);
  color: #0098BB;
  text-decoration-color: #B4ECFE;
  text-decoration-line: underline;
  text-decoration-thickness: 10px;
  text-underline-offset: 0px;
  text-decoration-skip-ink: none;

  @include mq(tb) {
    font-size: min(calc(24 / 1180 * 100vw), 24px);
  }

  @include mq(sp) {
    font-size: min(calc(24 / 375 * 100vw), 24px);
    text-decoration-thickness: 7px;
    text-underline-offset: 0px;
  }
}


.lp-connect__problem-cta {
  margin-top: 48px;
  text-align: center;

  @include mq(sp) {
    margin-top: 32px;
  }
}