@use "./global" as *;

/* 機能セクション
***************************************************************/

.lp-connect__function {
  background: #E6F5F8;
  padding-block: 80px 84px;

  @include mq(sp) {
    padding-block: 48px 59px;
  }
}

.lp-connect__function-intro {
  font-size: 18px;
  font-weight: 400;
  line-height: calc(29.25 / 18);
  color: #413C3A;
  text-align: center;
  margin-top: 24px;

  @include mq(sp) {
    font-size: min(calc(18 / 375 * 100vw), 18px);
    margin-top: 16px;
    letter-spacing: -0.03em;
  }
}

.lp-connect__function-contents {
  width: 968px;
  max-width: 100%;
  margin-inline:auto;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 32px calc(33 / 968 * 100%);
  margin-top: 40px;

  @include mq(sp) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 32px;
    margin-top: 32px;
    padding-inline: 17px;
  }
}

.lp-connect__function-card {
  border-radius: 20px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 28px 10px 25px 10px;
  box-shadow: 2px 2px 15px -1px rgba(0, 0, 0, 0.10);
  background: #fff;
}

.lp-connect__function-card-img {
  width: 105px;
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 105 / 100;
  }
}

.lp-connect__function-card-title {
  font-size: 20px;
  font-weight: 600;
  line-height: calc(25 / 20);
  color: #2A2929;

  @include mq(tb) {
    font-size: min(calc(20 / 1180 * 100vw), 20px);
  }

  @include mq(sp) {
    font-size: min(calc(20 / 375 * 100vw), 20px);
  }
}

.lp-connect__function-cta {
  display: none;

  @include mq(sp) {
    display: block;
    text-align: center;
    margin-top: 35px;
  }
}