@use "./variables" as *;

// =================================
//  メディアクエリ
// =================================

$breakpoints: (
    //キー : 値
    'sp': "screen and (max-width: #{$breakpoint - 1px})",
    'tb': "screen and (max-width: 1180px)",
    'pc': "(min-width: #{$breakpoint * 1px})",
) !default;

@mixin mq($breakpoint: pc) {
    @media #{map-get($breakpoints, $breakpoint)} {
        @content;
    }
}

