@use "./global" as *;

/* セクション共通見出し
***************************************************************/
.lp-connect__section-head {
	display: flex;
  flex-direction: column;
  gap: 24px;
  align-items: center;
  justify-content: center;

  @include mq(sp) {
    gap: 16px;
  }
}

.lp-connect__section-head-deco {
  width: 183px;
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 183 / 36;
  }
}

.lp-connect__section-head-title {
  font-size: 36px;
  font-weight: 700;
  line-height: calc(40 / 36);
  color: #2A2929;

  @include mq(sp) {
    font-size: min(calc(30 / 375 * 100vw), 30px);
    text-align: center;
    line-height: 1.3;

    .lp-connect__recommend & {
      line-height: 1.6;
    }
  }
}
