//===========================================
//   フォント(400,700,900)
//===========================================

$ff-base: "Noto Sans JP", sans-serif;
$ff-Robot: "Roboto", sans-serif;

//===========================================
//   レイアウト (単位不要)
//===========================================
$pc-inner: 968;  // pc版inner幅
$pc-sidePadding: 40; //pc版サイドのpadding
$pc-inner-sum: calc($pc-inner + $pc-sidePadding * 2);
$breakpoint: 768; // ブレイクポイント (pc版カンプとsp版カンプの境界)
$sp-sidePadding: 20; //sp版サイドのpadding
$pc-camp: 1920; // pc版のカンプサイズを入力
$sp-camp: 375; // sp版のカンプサイズを入力
$sp-inner: 500;// 768px以下のinner幅を入力
$sp-inner-sum: calc($sp-inner + $sp-sidePadding * 2);

$sm-breakpoint: 500; // small版ブレイクポイント

//headerの高さ
$pc-header-height: 72;
$sp-header-height: 60;

//===========================================
//   カラー変数
//===========================================
$cl-text:#2A2929;
$cl-black-mild:#413C3A;
$cl-white:#ffffff;
$cl-pink:#EA526F;
$cl-blue:#0098BB;
$cl-light-blue:#E6F5F8;
$cl-bg:#F7F6F4;