@use "./global" as *;

/* 料金プランセクション
***************************************************************/
.lp-connect__price {
  background: #fff;
  padding-block: 80px;

  @include mq(sp) {
    padding-block: 48px 60px;
  }
}

.lp-connect__price-intro {
  font-size: 18px;
  line-height: 1.6;
  font-weight: 400;
  text-align: center;
  color: #413C3A;
  margin-top: 24px;

  @include mq(sp) {
    font-size: min(calc(18 / 375 * 100vw), 18px);
    margin-top: 16px;
  }
}

.lp-connect__price-contents {
  border: 2px solid #0098BB;
  border-radius: 24px;
  display: flex;
  position: relative;
  overflow: hidden;
  margin-top: 48px;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.07);

  @include mq(sp) {
    margin-top: 32px;
    border-radius: 16px;
  }
}

.lp-connect__price-card {
  width: 50%;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;

  &:first-child {
    &::before {
      content: '';
      position: absolute;
      background-image: repeating-linear-gradient(180deg, #DDD9D7, #DDD9D7 4px, transparent 4px, transparent 8px);
      top: 0;
      right: 0;
      width: 2px;
      height: 100%;
    }
  }
}

.lp-connect__price-card-head {
  display: grid;
  place-content: center;
  padding: 10px;
  background: #fff;
  width: 100%;
  min-height: 120px;

  @include mq(sp) {
    min-height: 90px;
  }
}

.lp-connect__price-card-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 1.6;
  color: #0098bb;
  text-transform: uppercase;

  @include mq(sp) {
    font-size: min(calc(17 / 375 * 100vw), 17px);
  }
}

.lp-connect__price-card-body {
  display: grid;
  place-content: center;
  background: #E9FBFF;
  width: 100%;
  min-height: 120px;
  padding: 10px;

  @include mq(sp) {
    min-height: 90px;
  }
}

.lp-connect__price-card-detail {
  display: flex;
  align-items: flex-end;
  justify-content: center;

  @include mq(sp) {
    flex-wrap: wrap;
    gap: 8px 0;
  }
}

.lp-connect__price-card-number {
  font-size: 48px;
  font-weight: bold;
  line-height: 1;
  color: #191817;

  @include mq(sp) {
    font-size: min(calc(36 / 375 * 100vw), 36px);
  }
}

.lp-connect__price-card-unit {
  font-size: 24px;
  font-weight: 700;
  color: #191817;
  margin-bottom: 2px;

  @include mq(sp) {
    font-size: min(calc(20 / 375 * 100vw), 20px);
  }
}

.lp-connect__price-card-unit--mini {
  font-size: 20px;
  font-weight: 400;
  color: #191817;
  margin-bottom: 2px;

  @include mq(sp) {
    font-size: min(calc(16 / 375 * 100vw), 16px);
  }
}


.lp-connect__price-babble {
  margin-top: 42px;
  border-radius: 24px;
  background: #0098BB;
  position: relative;
  padding: 18px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  line-height: calc(32 / 20);

  @include mq(tb) {
    font-size: 18px;
  }

  @include mq(sp) {
    font-size: min(calc(16 / 375 * 100vw), 16px);
    border-radius: 16px;
    padding: 14px 0 14px 13px;
  }

  &::before {
    content: '';
    position: absolute;
    background: url(../img/deco_price-triangle.webp) no-repeat center center / contain;
    width: 35px;
    height: 42px;
    top: -18px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.lp-connect__price-babble-text {
  position: relative;
  z-index: 1;
}

.lp-connect__price-attention {
  margin-top: 24px;
}

.lp-connect__price-attention-text {
  font-size: 16px;
  line-height: 1.6;
  color: #413C3A;
  font-weight: 400;

  @include mq(sp) {
    font-size: min(calc(16 / 375 * 100vw), 16px);
  }

  &.lp-connect__price-attention-text--bold {
    font-weight: 700;
  }
}