@use "./global" as *;

/* ヘッダー
***************************************************************/

.lp-connect__header {
  width: 100%;
  z-index: 100;
  background-color: $cl-white;
}

.lp-connect__header-inner {
  display: flex;
  gap: 20px;
  justify-content: space-between;
  padding-inline: 20px;
  padding-block: 8px;
  max-width: calc(1280px + 40px);
  margin-inline:auto;
  align-items: center;
  height: px($pc-header-height);

  @include mq(sp) {
    height: px($sp-header-height);
  }
}

.lp-connect__header-logo {
  display: inline-block;
  img {
    width: min(calc(213 / 1440 * 100vw), 213px);
    height: auto;
    aspect-ratio: 213 / 56;
  }

  @include mq(tb) {
    img {
      width: min(calc(213 / 1180 * 100vw), 213px);
    }
  }

  @include mq(sp) {
    img {
      width: min(calc(160 / 375 * 100vw), 160px);
    }
  }
}

.lp-connect__header-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 30px;
}

.lp-connect__header-nav-list {
  display: flex;
  align-items: center;
  gap: 24px;

  a {
    font-size: 14px;
    font-weight: 700;
    line-height: 1.6;
    white-space: nowrap;
    color: $cl-text;
    text-decoration: none;
    transition: all  0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        opacity: 0.7;
      }
    }
  }

  @include mq(tb) {
    display: none;
  }
}

/* ヘッダー用CTA
***************************************************************/
.lp-connect__header-nav-cta {
  display: flex;
  align-items: center;
  gap: 16px;
}

.lp-connect__header-button {
  display: grid;
  place-content: center;
  width: 150px;
  height: 48px;
  border: 2px solid transparent;
  border-radius: 100vmax;
  background: $cl-pink;
  color: $cl-white;
  font-size: 14px;
  font-weight: 700;
  transition: all  0.3s ease;
  text-decoration: none;

  @include mq(tb) {
    display: none;
  }

  @include mq(sp) {
    font-size: min(calc(14 / 375 * 100vw), 14px);
  }

  @media (any-hover: hover) {
    &:hover {
      border: 2px solid $cl-pink;
      background: $cl-white;
      color: $cl-pink;
    }
  }

  &.lp-connect__header-button--trial,
  &.lp-connect__header-button--login {
    border: 2px solid #0098BB;
    background: $cl-white;
    color: #0098BB;

    @media (any-hover: hover) {
      &:hover {
        border: 2px solid #0098BB;
        background: #0098BB;
        color: #fff;
      }
    }
  }

  &.lp-connect__header-button--login {
    display: none;

    @include mq(tb) {
      display: grid;
      width: 80px;
      height: 38px;
    }
  }
}



