@use "./global" as *;

/* Aboutセクション
***************************************************************/
.lp-connect__about {
  background: #fff;
  position: relative;
  z-index: 1;
}

.lp-connect__about-inner {
  max-width: calc(1000px + 80px);
  margin-inline:auto;
  padding-block: 68px 33px;
  padding-inline: 40px;

  @include mq(sp) {
    max-width: calc(500px + 40px);
    padding-block: 57px 50px;
    padding-inline: 20px;
  }
}

.lp-connect__about-title {
  font-size: 33px;
  font-weight: 900;
  letter-spacing: 0.05em;
  line-height: 1;
  text-box: trim-both cap alphabetic;
  color: #fff;
  z-index: 2;
  position: absolute;
  top: -15px;
  left: 50%;
  translate: -50% 0%;
  width: max-content;

  @include mq(sp) {
    font-size: min(calc(26 / 375 * 100vw) ,26px);
    top: -9px;
  }

  .lp-connect__about-title--small {
    font-size: 28px;

    @include mq(sp) {
      font-size: min(calc(22 / 375 *100vw), 22px);
    }
  }

  &::before {
    content: '';
    position: absolute;
    background: url(../img/deco_about-bubble--pc.webp) no-repeat center center / contain;
    width: 558px;
    height: 83px;
    left: 50%;
    translate: -50% 0;
    top: -23px;
    z-index: -1;

    @include mq(sp) {
      background: url(../img/deco_about-bubble--sp.webp) no-repeat center center / contain;
      width: min(calc(335 / 375 * 100vw), 335px);
      height: 73px;
    }
  }
}

.lp-connect__about-concept {
  font-size: 34px;
  font-weight: 700;
  color: #2A2929;
  line-height: 1.6;
  letter-spacing: 0.05em;
  text-align: center;

  @include mq(tb) {
    font-size: min(calc(34 / 1180 * 100vw), 34px);
  }

  @include mq(sp) {
    font-size: min(calc(28 / 375 * 100vw), 28px);
    line-height: 1.6;
    letter-spacing: 0.02em;
    font-feature-settings: "palt" 1;
  }
}

.lp-connect__about-concept--blue {
  color: #0098BB;
  font-weight: 900;
}

.lp-connect__about-contents {
  display: flex;
  gap: calc(80 / 1000 * 100%);
  margin-top: 13px;

  @include mq(sp) {
    flex-direction: column;
    gap: 57px;
    margin-top: 10px;
  }
}

.lp-connect__about-card {
  width: calc(280 / 1000 * 100%);
  display: flex;
  flex-direction: column;
  gap: 15px;
  position: relative;

  @include mq(sp) {
    width: calc(260 / 335 * 100%);
    align-self: center;
    gap: 20px;

    &:not(:last-child) {
      &::before {
        content: '';
        position: absolute;
        background: url(../img/deco_about-circle--sp.webp) no-repeat center center / contain;
        top: calc(100% + 23px);
        left: 50%;
        translate: -50% 0;
        width: 8px;
        height: 38px;
      }
    }
  }
}

.lp-connect__about-card {//円のあしらい(PCのみ)
  &:not(:last-child) {
    .lp-connect__about-card-img {
      &::before {
        content: '';
        position: absolute;
        background: url(../img/deco_about-circle--pc.webp) no-repeat center center / contain;
        top: 50%;
        left: calc(100% + 2px);
        width: 68px;
        height: 8px;

        @include mq(sp) {
          display: none;
        }
      }
    }
  }
}

.lp-connect__about-card-img {
  position: relative;
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 1;
  }
}

.lp-connect__about-card-text {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.6;
  color: #2A2929;
  text-align: center;
  text-box: trim-both cap alphabetic;

  @include mq(tb) {
    font-size: min(calc(18 / 1180 * 100vw), 18px);
  }

  @include mq(sp) {
    font-size: min(calc(18 / 375 * 100vw), 18px);
  }
}

.lp-connect__about-cta {
  background: linear-gradient(144deg, #F0F9FB 0%, #E5F5F9 68%, #D7EEF3 100%);
  padding-block: 49px 65px;
  text-align: center;

  @include mq(sp) {
    padding-block: 32px 59px;
  }
}