@use "./global" as *;

/* mainレイアウト
***************************************************************/
.lp-connect__main {
	overflow: hidden;
	width: 100%;
}

/* 共通innerレイアウト
***************************************************************/
.lp-connect__inner {
  position: relative;
  width: 100%;
  max-width: px($pc-inner-sum);
  height: inherit;
  padding: 0 px($pc-sidePadding);
  margin-inline: auto;

  @include mq(sp) {
    width: 100%;
    max-width: px($sp-inner-sum);
    padding: 0 px($sp-sidePadding);
  }
}

/* ディスプレイ表示切り替え（ユーティリティ）
***************************************************************/
@include mq(pc) {
  .lp-connect__sp-only {
    display: none !important;
  }
}

@include mq(sp) {
  .lp-connect__pc-only {
    display: none !important;
  }
}