@use "./global" as *;

/* フッター
***************************************************************/
.lp-connect__footer {
  background: #E3F5FA;
  padding-block: 80px;

  @include mq(sp) {
    padding-block: 48px 60px;
    margin-bottom: 80px;
  }
}

.lp-connect__footer-cta {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr calc(402 / 968 * 100%);
  column-gap: 20px;
  row-gap: 32px;
  grid-auto-rows: auto;
  grid-template-areas:
    "title banner"
    "text banner"
    "links banner";

  @include mq(sp) {
    grid-template-columns: 1fr;
    row-gap: 32px;
    grid-template-areas:
      "title"
      "text"
      "banner"
      "links";
  }
}

.lp-connect__footer-title {
  font-size: 30px;
  font-weight: 700;
  line-height: calc(39 / 30);
  color: $cl-blue;
  grid-area: title;

  @include mq(sp) {
    font-size: min(calc(30 / 375 * 100vw),30px);
    line-height: calc(39 / 30);
    text-align: center;
  }
}

.lp-connect__footer-cta-text {
  font-size: 17px;
  font-weight: 400;
  line-height: calc(27.2 / 17);
  color: $cl-black-mild;
  grid-area: text;

  @include mq(tb) {
    font-size: 16px;
  }

  @include mq(sp) {
    font-size: min(calc(14 / 375 * 100vw),14px);
  }
}

.lp-connect__footer-cta-banner {
  grid-area: banner;
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 402 / 226;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.20)
  }
}

.lp-connect__footer-cta-links {
  grid-area: links;
  display: flex;
  flex-direction: column;
  gap: 16px;

  @include mq(sp) {
    gap: 20px;
    align-items: center;
  }
}

.lp-connect__footer-bottom {
  display: grid;
  align-items: center;
  grid-template-columns: 118px 1fr;
  column-gap: calc(80 / 968 * 100%);
  row-gap: 16px;
  grid-auto-rows: auto;
  grid-template-areas:
    "marks logo"
    "marks nav"
    "marks copyright";
  margin-top: 80px;

  @include mq(sp) {
    margin-top: 40px;
    grid-template-columns: 1fr calc(104 / 334 * 100%);
    column-gap: 40px;
    row-gap: 0;
    grid-template-areas:
    "nav nav"
    "logo marks"
    "copyright marks";
  }
}

.lp-connect__footer-marks {
  grid-area: marks;
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 118 / 105;
  }

  @include mq(sp) {
    margin-top: 44px;
  }
}

.lp-connect__footer-logo {
  grid-area: logo;
  width: 132px;
  img {
    width: 100%;
    height: auto;
    aspect-ratio: 132 / 32;
  }

  @include mq(sp) {
    width: 156px;
    margin-top: 51px;
  }
}

.lp-connect__footer-nav {
  grid-area: nav;
  display: flex;
  gap: 27px;

  @include mq(sp) {
    flex-direction: column;
    gap: 25px;
  }
}

.lp-connect__footer-nav-item {
  >a {
    display: inline-block;
    font-size: 14px;
    font-weight: 400;
    line-height: calc(24 / 14);
    padding-inline: 4px;
    color: $cl-black-mild;
    text-decoration: none;
    transition: all  0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        cursor: pointer;
        opacity: 0.7;
      }
    }
  }
}

.lp-connect__footer-copyright {
  grid-area: copyright;
  font-size: 12px;
  font-weight: 400;
  line-height: calc(16.8 / 12);
  color: #908883;
  font-family: $ff-Robot;
  display: flex;
  gap: 16px;

  @include mq(sp) {
    flex-direction: column;
    gap: 0;
    margin-top: 10px;
  }
}
