@use "./global" as *;

/* セクション共通ボタン（基本・ピンク）
***************************************************************/
.lp-connect__button {
  width: 360px;
  height: 60px;
  max-width: 100%;
  display: inline-grid;
  grid-template-columns: auto auto;
  justify-content: center;
  align-items: center;
  gap: 12px;
  border: 2px solid transparent;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.10);
  border-radius: 100vmax;
  background: $cl-pink;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  transition: all  0.3s ease;
  text-decoration: none;
  text-box: trim-both cap alphabetic;

  @media (any-hover: hover) {
    &:hover {
      background-color: #fff;
      color: $cl-pink;
      border: 2px solid $cl-pink;

      .lp-connect__button-icon path{
        fill:$cl-pink;
      }
    }
  }

  @include mq(tb) {
    font-size: 16px;
  }

  @include mq(sp) {
    font-size: min(calc(18 / 375 * 100vw), 18px);
    width: min(calc(335 / 375 * 100vw), 335px);
    height: min(calc(60 / 375 * 100vw), 60px);
    max-width: 100%;
  }
}

.lp-connect__button-icon {
  translate:0 2px;
}

.lp-connect__button-icon path{
  transition: all  0.3s ease;
}

/* セクション共通ボタン（白枠）
***************************************************************/
.lp-connect__button {
  &.lp-connect__button--outline {
    border: 2px solid #fff;
    @media (any-hover: hover) {
      &:hover {
        color: $cl-pink;
        border: 2px solid $cl-pink;
      }
    }
  }
}

/* セクション共通ボタン（青枠）
***************************************************************/
.lp-connect__button {
  &.lp-connect__button--blue {
    gap: 30px;
    border: 2px solid #0098BB;
    background: #fff;
    color: #0098BB;
    @media (any-hover: hover) {
      &:hover {
        background-color: #0098BB;
        color: #fff;
        .lp-connect__button-icon path{
          fill:#fff;
        }
      }
    }
  }
}

/* セクション共通ボタン（FV用）
***************************************************************/
.lp-connect__fv-cta {
  .lp-connect__button {
    width: 247px;

    @include mq(sp) {
      // width: 160px;
      height: 48px;
      font-size: min(calc(14 / 375 * 100vw), 14px);
      gap: 0;
      box-shadow: none;
    }
  }
}

/* セクション共通ボタン（フッター用ミニサイズ）
***************************************************************/
.lp-connect__footer-cta-links {
  .lp-connect__button {
    width: 330px;

    @include mq(sp) {
      width: min(calc(335 / 375 * 100vw), 335px);
    }
  }
}

/* セクション共通ボタン（SP用 下部固定ボタン）
***************************************************************/
.lp-connect__fixed-cta {
  .lp-connect__button {
    width: 247px;

    @include mq(sp) {
      // width: 160px;
      height: 48px;
      font-size: min(calc(14 / 375 * 100vw), 14px);
      gap: 0;
      box-shadow: none;
    }
  }
}